/*********************************************** FILTERS **************************************************/
#kpi {
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 30px;
  display: inline-grid;
}
#kpi .title {
    font-size: 12px;
    margin-bottom: 16px;
    font-weight: 600;
}
#kpi .info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 10px;
}
#kpi .info .icon {
      height: 19px;
      width: 41px;
      margin-right: 10px;
      display: inline-block;
      -webkit-transition: 0.4s all linear;
      transition: 0.4s all linear;
}
#kpi .info .icon.loading {
        width: 0px;
        margin-right: 51px;
        -webkit-transition: 0s;
        transition: 0s;
}
#kpi .info .icon.negative {
        background: url(data:image/svg+xml;base64,);
}
#kpi .info .icon.positive {
        background: url(data:image/svg+xml;base64,);
}
#kpi .info .value {
      font-size: 32px;
      font-weight: 600;
      line-height: 32px;
      margin-right: 10px;
      -webkit-transition: 0s all linear;
      transition: 0s all linear;
      opacity: 1;
}
#kpi .info .value.loading {
        -webkit-transition: 0s;
        transition: 0s;
        opacity: 0;
}
#kpi .info .units {
      -ms-flex-item-align: end;
          align-self: flex-end;
      font-size: 12px;
      -webkit-transition: 0s all linear;
      transition: 0s all linear;
      opacity: 1;
}
#kpi .info .units.loading {
        -webkit-transition: 0s;
        transition: 0s;
        opacity: 0;
}
#kpi .relative {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#kpi .relative .value {
      font-size: 14px;
      font-weight: 600;
      color: #414141;
      margin-right: 10px;
      -webkit-transition: 0.5s opacity linear;
      transition: 0.5s opacity linear;
      opacity: 1;
}
#kpi .relative .value.loading {
        color: #414141;
        -webkit-transition: 0s;
        transition: 0s;
        opacity: 0;
}
#kpi .relative .units {
      font-size: 10px;
      -ms-flex-item-align: end;
          align-self: flex-end;
      -webkit-transition: 0s opacity linear;
      transition: 0s opacity linear;
      opacity: 1;
}
#kpi .relative .units.loading {
        -webkit-transition: 0s;
        transition: 0s;
        opacity: 0;
}
#kpi .relative .icon {
      position: relative;
      width: 12px;
      height: 9px;
      margin-right: 10px;
}
#kpi .relative .icon:after {
        content: "";
        position: absolute;
        left: 5px;
}
#kpi .relative .icon:before {
        content: "";
        position: absolute;
        left: 2px;
}
#kpi .relative.positive .value {
      color: #63A621;
}
#kpi .relative.positive .icon.loading:after {
      border: 2px solid #63A621;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 0;
      width: 0px;
      height: 0px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-transition: 0s;
      transition: 0s;
}
#kpi .relative.positive .icon.loading:before {
      top: 8px;
      border: 2px solid #63A621;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 0;
      width: 0px;
      height: 0px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      -webkit-transition: 0s;
      transition: 0s;
}
#kpi .relative.positive .icon:after {
      border: 2px solid #63A621;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 1;
      width: 0px;
      height: 9px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-transition: 0.5s height linear;
      transition: 0.5s height linear;
      bottom: -2px;
}
#kpi .relative.positive .icon:before {
      border: 2px solid #63A621;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 1;
      width: 6px;
      height: 6px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      -webkit-transition: 0.5s top linear;
      transition: 0.5s top linear;
      top: 0px;
}
#kpi .relative.negative .value {
      color: #CC0000;
}
#kpi .relative.negative .icon.loading:after {
      border: 2px solid #CC0000;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 0;
      width: 0px;
      height: 0px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-transition: 0s;
      transition: 0s;
}
#kpi .relative.negative .icon.loading:before {
      top: -7px;
      border: 2px solid #CC0000;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 0;
      width: 0px;
      height: 0px;
      -webkit-transform: rotate(225deg);
              transform: rotate(225deg);
      -webkit-transition: 0s;
      transition: 0s;
}
#kpi .relative.negative .icon:after {
      border: 2px solid #CC0000;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 1;
      width: 0px;
      height: 9px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      -webkit-transition: 0.5s height linear;
      transition: 0.5s height linear;
      top: -2px;
}
#kpi .relative.negative .icon:before {
      top: 1px;
      border: 2px solid #CC0000;
      border-right: 0px;
      border-bottom: 0px;
      opacity: 1;
      width: 6px;
      height: 6px;
      -webkit-transform: rotate(225deg);
              transform: rotate(225deg);
      -webkit-transition: 0.5s top linear;
      transition: 0.5s top linear;
}
@font-face {
  font-family: 'Open Sans';
