@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  src: url(../../static/fonts/OpenSans-SemiBold.e1c83f9.ttf), local("Open Sans Semibold"), local("OpenSans-Semibold");
}
body {
  letter-spacing: 0.02em;
  font-family: 'Open Sans', sans-serif !important;
  font-size: 14px;
  color: #414141;
  min-width: 1360px;
  overflow-x: hidden;
}
.container {
  margin: auto;
  max-width: 100%;
  position: relative;
  width: 1200px;
  background-color: #F9F9F9;
}
.container > object {
    pointer-events: none;
}
.bold {
  font-weight: 600;
}
div.tryme {
  position: absolute;
  top: 160px;
  left: 1044px;
  line-height: 14px;
  font-size: 14px;
  background-color: #fff;
  padding-left: 23px;
  padding-right: 23px;
  border: 1px solid #CC0000;
  color: #CC0000;
  font-weight: 600;
  width: auto;
  height: 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-user-select: none;
  -moz-user-select: none;
}
div.tryme:hover {
    cursor: pointer;
    color: #fff;
    background-color: #CC0000;
    -webkit-transition: 0.2s all linear;
    transition: 0.2s all linear;
}
div.tryme:active {
    line-height: 15px;
    color: #CC0000;
    background-color: #fff;
}
div.tryme:focus {
    outline: none;
}
